<template class="index">
	<div id="vue">
		<van-divider :style="{ color: '#1989fa', borderColor: '#1989fa'}">
			居民信息
		</van-divider>
		<form>
			<listing>姓名</listing>
			<van-cell-group>
				<van-field placeholder="请输入用户名" v-model="person.name" />
			</van-cell-group>
			<listing>身份证号</listing>
			<van-cell-group>
				<van-field placeholder="请输入身份证号" v-model="person.idcard" v-on:click="getOtherinformationListApi()" />
			</van-cell-group>
			<listing>性别</listing>
			<van-cell-group>
				<van-field placeholder="请选择性别" v-model="person.sex" v-on:click="clicksex()" readonly="readonly" />
			</van-cell-group>

			<listing>出生日期</listing>
			<van-cell-group>
				<van-field placeholder="请选择出生日期" v-model="person.birday" v-on:click="clickdate()" readonly="readonly" />
			</van-cell-group>

			<listing>本人联系电话</listing>
			<van-cell-group>
				<van-field placeholder="请输入本人联系电话" v-model="person.phone" />
			</van-cell-group>

			<listing>现住址</listing>
			<van-cell-group>
				<van-field placeholder="请输入现住址" v-model="person.address" />
			</van-cell-group>

			<listing>其它档案人员信息</listing>
			<van-checkbox-group v-model="person.checkboxvalue">
				<van-checkbox :name="list.other_id" v-for="list in OtherinformationList">{{list.other_name}}</van-checkbox>
			</van-checkbox-group>
			<van-divider :style="{ color: '#ff976a', borderColor: '#ff976a'}">
				签约信息
			</van-divider>
			<listing>有效期开始日期</listing>
			<van-cell-group>
				<van-field placeholder="请选择有效期开始日期" v-model="person.startDate" v-on:click="clickstartDate()" readonly="readonly" />
			</van-cell-group>
			<listing>有效期截止日期</listing>
			<van-cell-group>
				<van-field placeholder="请选择有效期截止日期" v-model="person.endDate" v-on:click="clickendDate()" readonly="readonly" />
			</van-cell-group>
			<listing>签约日期</listing>
			<van-cell-group>
				<van-field placeholder="请选择签约日期" v-model="person.SigningDate" v-on:click="clickSigningDate()" readonly="readonly" />
			</van-cell-group>
			<listing>家医团队</listing>
			<van-cell-group>
				<van-field placeholder="请选择家医团队" v-model="person.team" v-on:click="clickteam()" readonly="readonly" />
			</van-cell-group>
			<listing>团队医生</listing>
			<van-cell-group>
				<van-field placeholder="请选择团队医生" v-model="person.doctor" v-on:click="clickdoctor()" readonly="readonly" />
			</van-cell-group>

			<van-divider :style="{ color: '#07c160', borderColor: '#07c160'}">
				人群类别
			</van-divider>

			<van-checkbox-group v-model="person.Crowdvalue">
				<van-checkbox :name="list.crowdId" v-for="list in CrowdclassificationList">{{list.crowdName}}</van-checkbox>
			</van-checkbox-group>
			<van-divider :style="{ color: '#ee0a24', borderColor: '#ee0a24'}">
				服务包
			</van-divider>
			<listing class="minlisting">增值服务包</listing>
			<listing class="minlisting">无</listing>
			<div style="height: 5px;"></div>
			<listing class="minlisting">基础服务包:</listing>
			<listing class="minlisting">统一指定服务，无需选择</listing>
			<div style="height: 2px;"></div>
			<listing>应收金额(元)</listing>
			<van-cell-group>
				<van-field v-model="person.receivablemoney" />
			</van-cell-group>

			<van-divider :style="{ color: '#1989fa', borderColor: '#1989fa'}" />

			<listing class="minlisting">合同编号:保存后自动生成</listing>
			<listing class="minlisting">纸质合同号：(用于打印)</listing>
			<van-cell-group>
				<van-field v-model="person.Contract" />
			</van-cell-group>
			<van-checkbox v-model="person.Contractchecked">同合同编码</van-checkbox>

			<van-divider :style="{ color: '#1989fa', borderColor: '#1989fa'}" />
			<listing>签约备注</listing>
			<van-cell-group>
				<van-field type="textarea" rows="4" autosize v-model="person.demo" />
			</van-cell-group>
			<br />
			<van-button type="info" style="width: 100%;">提交</van-button>
		</form>
		<van-action-sheet v-model="sexshow" :actions="sexactions" @select="onsexSelect" title="性别" />

		<van-action-sheet v-model="teamshow" :actions="teamactions" @select="onteamSelect" @close="getDoctorListApi" :title="teamTitle" />

		<van-action-sheet v-model="doctorshow" :actions="doctoractions" @select="ondoctorSelect" />

		<van-popup v-model="datashow" position="bottom">
			<van-datetime-picker v-model="currentDate" type="date" @change="selectdata" @cancel="datashow = false" @confirm="datashow = false" />
		</van-popup>

		<van-popup v-model="startshow" position="bottom">
			<van-datetime-picker v-model="startDate" type="date" @change="selectstartDate" @cancel="startshow = false" @confirm="startshow = false" />
		</van-popup>

		<van-popup v-model="endshow" position="bottom">
			<van-datetime-picker v-model="endDate" type="date" @change="selectendDate" @cancel="endshow = false" @confirm="endshow = false" />
		</van-popup>

		<van-popup v-model="Signingshow" position="bottom">
			<van-datetime-picker v-model="SigningDate" type="date" @change="selectSigning" @cancel="Signingshow = false" @confirm="Signingshow = false" />
		</van-popup>

	</div>
</template>

<script>
	import $ from 'jquery'
	import { Toast } from 'vant';

	export default {
		name: 'HelloWorld',
		data() {
			return {
				OtherinformationList:[],
				CrowdclassificationList:[],
				teamTitle: "胜利社区卫生服务中心",
				currentDate: new Date(),
				startDate: new Date(),
				endDate: new Date(),
				SigningDate: new Date(),
				person: {
					name: "",
					idcard: "",
					sex: "",
					checkboxvalue: [],
					Crowdvalue: [],
					sexvalue: "",
					birday: "",
					phone: "",
					address: "",
					startDate: "",
					endDate: "",
					SigningDate: "",
					team: "",
					teamvalue: "",
					doctor: "",
					doctorvalue: "",
					receivablemoney: "0",
					Contract: "",
					Contractchecked: true,
					demo: ""
				},
				value: 0,
				sexshow: false,
				teamshow: false,
				datashow: false,
				startshow: false,
				doctorshow: false,
				endshow: false,
				Signingshow: false,
				sexactions: [{
						name: '未知的性别',
						value: 0
					},
					{
						name: '男',
						value: 1

					},
					{
						name: '女',
						value: 2
					},
					{
						name: '未说明的性别',
						value: 9
					}
				],
				teamactions: [],
				doctoractions: []
			}
		},
		methods: {
			onsexSelect(item) {
				// 点击选项时默认不会关闭菜单，可以手动关闭
				this.person.sex = item.name;
				this.person.sexvaule = item.value;
				this.sexshow = false;

			},
			onteamSelect(item) {
				this.person.team = item.name;
				this.person.teamvalue = item.value;
				this.teamshow = false;

			},
			ondoctorSelect(item) {
				this.person.doctor = item.name;
				this.person.doctorvalue = item.value;
				this.doctorshow = false;

			},
			clicksex() {
				this.sexshow = true;

			},
			clickteam() {
				this.teamshow = true;

			},
			clickdoctor() {
				
				if(this.doctoractions.length >0){
					this.doctorshow = true;
				}else{
					if(this.person.teamvalue == ""){
						Toast('请选择医生团队');
					}else{
						Toast('该团队下暂无医生请选择其它团队');
					}
				}
				
				

			},
			clickdate() {
				this.datashow = true;
			},
			selectdata(e) {

				let endTimeArr = e.getValues();
				var date = endTimeArr[0] + '-' + endTimeArr[1] + '-' + endTimeArr[2];
				this.person.birday = date;
			},
			selectstartDate(e) {

				let endTimeArr = e.getValues();
				var date = endTimeArr[0] + '-' + endTimeArr[1] + '-' + endTimeArr[2];
				this.person.startDate = date;
			},
			selectendDate(e) {

				let endTimeArr = e.getValues();
				var date = endTimeArr[0] + '-' + endTimeArr[1] + '-' + endTimeArr[2];
				this.person.endDate = date;
			},
			selectSigning(e) {

				let endTimeArr = e.getValues();
				var date = endTimeArr[0] + '-' + endTimeArr[1] + '-' + endTimeArr[2];
				this.person.SigningDate = date;
			},
			clickstartDate() {
				this.startshow = true;
			},
			clickendDate() {
				this.endshow = true;
			},
			clickSigningDate() {
				this.Signingshow = true;
				
			},
			getOtherinformationListApi() {
				var that = this;
				$.ajax({    
					type: "GET",
					url: this.ApiUrl+"/Otherinformation/getOtherinformationListApi",
					dataType: "json",
					async:false,
					success: function(data) { 
						      
						if(data.stateCode == "200"){
							that.OtherinformationList = data.list;
						}
					}
				});

			},
			getCrowdclassificationListApi() {
				var that = this;
				$.ajax({    
					type: "GET",
					url: this.ApiUrl+"/Otherinformation/getCrowdclassificationListApi",
					dataType: "json",
					async:false,
					success: function(data) { 
						      
						if(data.stateCode == "200"){
							that.CrowdclassificationList = data.list;
						}
					}
				});

			},
			getDoctorTeamListApi(){
				var that = this;
				$.ajax({    
					type: "POST",
					url: this.ApiUrl+"/Otherinformation/getDoctorTeamListApi",
					dataType: "json",
					data:{"areaCode":"630102001001"},
					async:false,
					success: function(data) { 
						if(data.stateCode == "200"){
							that.teamactions = data.list;
							
						}
					}
				});
			},
			getDoctorListApi(){
				
				var that = this;
				
				if(that.person.teamvalue != ""){
					$.ajax({    
						type: "POST",
						url: this.ApiUrl+"/Otherinformation/getDoctorListApi",
						dataType: "json",
						data:{"DUTYID":that.person.teamvalue},
						async:false,
						success: function(data) { 
							
							if(data.stateCode == "200"){
								that.doctoractions = data.list;
								
							}else{
								that.doctoractions = [];
							}
						}
					});
				}
			}

		},
		mounted: function(){
			this.getOtherinformationListApi();
			this.getCrowdclassificationListApi();
			this.getDoctorTeamListApi();
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.index {
		margin: 0px 10px 0px 10px;
	}
	
	.van-checkbox {
		margin: 5px 0 0 0px;
	}
	
	.minlisting {
		line-height: 5px;
		height: 5px;
	}
</style>